import { setSStore,removeSStore,getSStore } from "@/utils/util"

import Load from '@/components/Load.vue'
import flight from './components/flight.vue'
import hotel from './components/hotel.vue'
import train from './components/train.vue'

export default {
	data() {
		return {
			payType:null,//支付来源			
			count:null,//支付的总价
			paying:false,//正在支付
		}
	},
	computed: {
		
	},
	watch:{
		
	},
	methods: {
		back(){
			this.$router.go(-1)
		},
		pay(){
			this.paying = true;
			if(this.payType=='hotel'){
				this.hotelPay();
			}
			if(this.payType=='flight'){
				this.flightPay();
			}
			if(this.payType=='train'){
				this.trainPay();
			}
		},
		hotelPay(){
			let orderId = this.$route.query.OrderId;
			let hotelOrderData = JSON.parse(getSStore('hotelOrderData'));

		    if(hotelOrderData){		    	
		        let par = {
			        orderId: orderId,
			        id:      hotelOrderData.hotelRouteId
			    }
		    	//发送数据 start			
				
				this.$http.post("/pay/hotelPay",par)
					.then((res) => {
						this.paying = false;
						let status = res.data.status;
						if(status&&status==1001){							
							this.$Notice.success({
			                    title: '支付成功',
			                    desc: res.data.message
			                });
			                this.$router.push({ path: '/home/task/travelbooking' }); 
						}else{
							this.$Notice.error({
			                    title: '支付失败',
			                    desc: res.data.message
			                });
						}
					})						
					//发送数据 end	
		   }			
		},
		flightPay(){
			let [id,orderNumber] = [getSStore('flightRouteId'),getSStore('flightOrderNo')];
			this.$http.post('pay/tmcPay',{'id':id,'orderNumber':orderNumber})
				.then((res) => {
					this.paying = false;
					let success = res.data.success;
					if(success){
						this.$Notice.success({
		                    title: '支付成功',
		                    desc: res.data.msg
		                });
		                this.$router.push({ path: '/home/task/travelbooking' }); 
					}else{
						this.$Notice.error({
		                    title: '支付失败',
		                    desc: res.data.msg
		                });
					}
				})				
		},
		trainPay(){
			this.$http.post('train/pay',{'orderId':JSON.parse(getSStore('train_orderData')).merchant_order_id})
				.then((res) => {
					this.paying = false;
					let status = res.data.status;
					if(status==1001){						
		                this.$notify.success({
				            title: '支付成功',
				            message: '请关注发车时间',
				        });
					}else{						
		                this.$notify.error({
				            title: '支付失败',
				            message: res.data.data.message
				        });
					}
				})			
		},
	},
	components:{
		flight,
		hotel,
		train,
		Load
	},
	created() {
		this.payType = this.$route.query.type;
		if(this.payType=='hotel'){
			
			let roomRate = JSON.parse(getSStore('payInfo')).roomRate;//酒店总价
			let roomNum = JSON.parse(getSStore('payInfo')).roomNum;//房间数
			this.count = roomRate*roomNum;

		}else if(this.payType=='flight'){
			
			let price = JSON.parse(getSStore('flight_orderConfirm')).adultFacePrice;//机票价格
			let flightPassengersCount = JSON.parse(getSStore('flight_passengers')).length;//乘机人数
			this.count = price*flightPassengersCount;
		}else if(this.payType=='train'){
			
			this.count = JSON.parse(getSStore('train_orderData')).pay_money;//火车票总价
		}
	}
}


